
@import '../assets/reset.css';
.home {
		width: 100%;
		height: 100%;
		position: relative;
}
.intro {
		display: block;
		position: absolute;
		width: 80%;
		left: 50%;
		top: 50%;
		color: #fff;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
}
.title, .info, .more{
		margin-bottom: 20px;
}
.more {
		padding-right: 8px;
		text-align: right;
}



